import React from 'react';
import {
  Switch,
  Route,
  withRouter,
} from 'react-router-dom';
import { ROUTES } from 'constants/routes';
import withTracker from 'withTracker';
// import {
//   Landing,
//   NotFound,
//   NCalculator,
// } from 'pages';

const Landing = React.lazy(() => import('pages/Landing'));
const NotFound = React.lazy(() => import('pages/NotFound'));
const NCalculator = React.lazy(() => import('pages/NCalculator'));
const Blog = React.lazy(() => import('pages/Blog'));

const Routes = () => (
  <React.Suspense fallback={<div />}>
    <Switch>
      <Route exact path={ROUTES.LANDING.link} component={Landing} />
      <Route exact path={ROUTES.NCALCULATOR.link} component={NCalculator} />
      <Route exact path={`${ROUTES.BLOG.link}/:blog_page`} component={Blog} />
      <Route exact path={`${ROUTES.BLOG.link}`} component={Blog} />
      <Route path="*" component={NotFound} />
    </Switch>
  </React.Suspense>
);

export default withRouter(withTracker(Routes));
