import React, { useEffect, useState } from 'react';
import { BrowserRouter } from 'react-router-dom';
import Routes from './Routes';

const FBMessenger = React.lazy(() => import('components/FBMessenger'));

const App = () => {
  const [displayFB, setFB] = useState(false);

  useEffect(() => {
    setTimeout(() => setFB(true), 3000);
  }, []);

  return (
    <React.Suspense fallback={<div />}>
      <BrowserRouter>
        <Routes />
      </BrowserRouter>
      <FBMessenger />
      {/* {displayFB && <FBMessenger />} */}
    </React.Suspense>
  )
}

export default App;
