/**
 * From ReactGA Community Wiki Page https://github.com/react-ga/react-ga/wiki/React-Router-v4-withTracker
 */

import React, { Component } from 'react';
import ReactGA from 'react-ga';
// import { isProduction } from 'lib/tagFunctions';

const ops = {
  debug: process.env.REACT_APP_IS_PRODUCTION === 'false',
};

const TRACKER_ENABLED = {
  GA: !!process.env.REACT_APP_GA_TAG,
};

// if (isProduction) {
  process.env.REACT_APP_GA_TAG && ReactGA.initialize(process.env.REACT_APP_GA_TAG, ops);
// }
// TRACKER_ENABLED.FB_PIXEL && ReactPixel.init(process.env.REACT_APP_FB_PIXEL, {}, ops);
// if (!isProduction) {
// TRACKER_ENABLED.GTM && TagManager.initialize({gtmId: process.env.REACT_APP_GTM});
// }

export default (WrappedComponent, options = {}) => {
  const getPath = (location) => {
    const { pathname, search } = location;
    return `${pathname}${search}`;
  }

  const trackPage = (page) => {
    if (TRACKER_ENABLED.GA) {
      ReactGA.set({ ...options, page });
      ReactGA.pageview(page);
    }
    // if (TRACKER_ENABLED.FB_PIXEL) {
    //   ReactPixel.pageView();
    // }
  };

  const HOC = class extends Component {
    componentDidMount() {
      trackPage(getPath(this.props.location));
    }

    componentWillReceiveProps(nextProps) {
      const currentPage = getPath(this.props.location);
      const nextPage = getPath(nextProps.location);
  
      if (currentPage !== nextPage) {
        trackPage(nextPage);
      }
    }

    render() {
      return <WrappedComponent {...this.props} />;
    }
  };

  return HOC;
};
